@import 'normalize.css';

@font-face {
    font-family: 'TikTokFont';
    src: url('../../assets/fonts/TikTokFont-Bold.woff2'), url('../../assets/fonts/TikTokFont-Semibold.woff2');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'TikTokFont';
    src: url('../../assets/fonts/TikTokFont-Regular.woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'TikTokDisplayFont';
    src: url('../../assets/fonts/TikTokDisplayFont-Bold.woff2'),
        url('../../assets/fonts/TikTokDisplayFont-Semibold.woff2');
    font-weight: bold;
    font-style: normal;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    --default-font-color: #333;
    --sliderbar-max-width: 240px;
    --white: #fff;
    --svg-color: rgba(22, 24, 35, 0.34);
    --svg-color_2: rgba(22, 24, 35, 0.75);
    --header-height: 60px;
    --primary-color: rgb(254, 44, 85);
    --button-component-style-2-width: 32px;
}

html {
}

body {
    --text-color: #161823;
    font-family: 'TikTokFont', 'TikTokDisplayFont', 'Segoe UI';
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    color: var(--text-color);
    overflow: overlay;
    height: 5000px;
}

//scrollbar custom
html ::-webkit-scrollbar {
    border-radius: 0px;
    width: 8px;
}

html ::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(22, 24, 35, 0.06);
}

//tag

a,
p,
span,
button {
    color: var(--text-color);
    font-weight: normal;
}

h1,
h2,
h3,
h4 {
    font-weight: 600;
}

button,
input {
    border: none;
    outline: none;
    background-color: transparent;
    font-family: 'TikTokFont', 'TikTokDisplayFont', 'Segoe UI';
}

button {
    display: flex;
    align-items: center;
}

a[href] {
    text-decoration: none;
}

li {
    list-style: none;
}

//custom tippy tooltip
body .tippy-box {
    height: 38px;
    border-radius: 8px;
    background-color: rgba(84, 84, 84, 0.92);
    line-height: 2.2rem;
    display: flex;
    align-items: center;
}

body .tippy-arrow {
    color: rgba(84, 84, 84, 0.92);
    margin-top: -1px;
}
